          @tap="handleAuthAction"
        >
          {{ authed ? 'ÈÄÄÂá∫ÁôªÂΩï' : 'ÁôªÂΩï/Ê≥®ÂÜå' }}
        </button>
      </view>
    </view>

    <!-- ÂäüËÉΩËèúÂçï -->
    <view class="menu-section">
      <view class="menu-title">
