        </view>
        
        <view class="menu-item" @tap="navigateTo('/pages-sub/other/settings/settings')">
          <view class="menu-icon">‚öôÔ∏è</view>
          <text class="menu-text">Â∫îÁî®ËÆæÁΩÆ</text>
          <text class="menu-arrow">‚Ä∫</text>
        </view>
        
        <view class="menu-item" @tap="showSubscriptionSettings">
          <view class="menu-icon">üîî</view>
          <text class="menu-text">ËÆ¢ÈòÖËÆæÁΩÆ</text>
          <text class="menu-arrow">‚Ä∫</text>
        </view>
        
        <view class="menu-item" @tap="showFeedback">
          <view class="menu-icon">üí¨</view>
