        </view>
        
        <view class="shortcut-item" @tap="navigateTo('/pages-sub/other/redeem')">
          <view class="shortcut-icon">üéÅ</view>
          <text class="shortcut-text">CDKÂÖëÊç¢</text>
        </view>
        
        <view class="shortcut-item" @tap="navigateTo('/pages-sub/other/metrics')" v-if="isAdmin">
          <view class="shortcut-icon">üìà</view>
          <text class="shortcut-text">Êï∞ÊçÆÊåáÊ†á</text>
        </view>
        
        <view class="shortcut-item" @tap="navigateTo('/pages-sub/other/test/index')">
          <view class="shortcut-icon">üß™</view>
          <text class="shortcut-text">ÊµãËØïÈ°µÈù¢</text>
        </view>
      </view>
    </view>

    <!-- ËÆ¢ÈòÖËÆæÁΩÆÂºπÁ™ó -->
    <u-popup v-model="showSubscriptionPopup" mode="bottom" height="60%" border-radius="24" :safe-area-inset-bottom="true">
      <view class="popup-content">
        <view class="popup-header">
          <text class="popup-title">ËÆ¢ÈòÖËÆæÁΩÆ</text>
          <view class="popup-close" @tap="showSubscriptionPopup = false">
            <text class="close-icon">√ó</text>
          </view>
        </view>
        <view class="popup-body">
          <view class="setting-item">
            <text class="setting-label">ÂéãÂäõÊ£ÄÊµãÊèêÈÜí</text>
            <u-switch v-model="subscriptionSettings.stressReminder" @change="updateSubscription"></u-switch>
          </view>
          <view class="setting-item">
            <text class="setting-label">ÊØèÊó•ÂøÉÁêÜÂª∫ËÆÆ</text>
            <u-switch v-model="subscriptionSettings.dailyTips" @change="updateSubscription"></u-switch>
          </view>
          <view class="setting-item">
            <text class="setting-label">Á§æÂå∫Âä®ÊÄÅÈÄöÁü•</text>
            <u-switch v-model="subscriptionSettings.communityUpdates" @change="updateSubscription"></u-switch>
          </view>
        </view>
      </view>
    </u-popup>
    
    
  </view>
</template>

<script>
import { 
  isAuthed, 
  getLoginData, 
  clearLoginData,
  getUid,
  getUserInfo
} from '@/utils/auth.js';
import { authAPI, subscribeAPI } from '@/utils/request.js';
import tabBarManager from '@/utils/tabbar-manager.js';

export default {
  data() {
    return {
      // Âä†ËΩΩÁä∂ÊÄÅ
      isLoading: true,
      
      // Áî®Êà∑Áä∂ÊÄÅÊï∞ÊçÆ - Áõ¥Êé•Â≠òÂÇ®Ôºå‰∏ç‰ΩøÁî®ËÆ°ÁÆóÂ±ûÊÄß
      authed: false,
      name: 'Êú™ÁôªÂΩï',
      uid: '',
      avatar: '',
      
      // ÂºπÁ™óÁä∂ÊÄÅ
      showSubscriptionPopup: false,
      
      // ËÆ¢ÈòÖËÆæÁΩÆ
      subscriptionSettings: {
        stressReminder: false,
        dailyTips: false,
        communityUpdates: false
      }
    };
  },
  
  computed: {
    // Áä∂ÊÄÅÊñáÊú¨
    statusText() {
      return this.authed ? 'Â∑≤ÁôªÂΩï' : 'ÁÇπÂáªÁôªÂΩïËé∑ÂèñÂÆåÊï¥ÂäüËÉΩ';
    },
    
    // Â§¥ÂÉèÊñáÂ≠ó
    avatarText() {
      if (!this.authed) {
        return 'Êú™';
      }
      
      const name = this.name;
      return name.length > 0 ? name.charAt(0) : 'Áî®';
    },
    
    // ÊòØÂê¶ÁÆ°ÁêÜÂëò
    isAdmin() {
      // ÁÆÄÂçïÁöÑÁÆ°ÁêÜÂëòÂà§Êñ≠ÈÄªËæëÔºåÂèØÊ†πÊçÆÂÆûÈôÖÈúÄÊ±ÇË∞ÉÊï¥
      const loginData = getLoginData();
      return loginData.userInfo?.role === 'admin';
    }
  },
  
  onLoad() {
    console.log('[PROFILE] È°µÈù¢Âä†ËΩΩ');
    
    // Ê≥®ÂÜåÁôªÂΩïÁä∂ÊÄÅÂèòÊõ¥‰∫ã‰ª∂ÁõëÂê¨
    uni.$on('AUTH_CHANGED', this.onAuthChanged);
    
    this.refreshProfile();
  },
  
  onShow() {
    console.log('[PROFILE] È°µÈù¢ÊòæÁ§∫ÔºåÂà∑Êñ∞Áî®Êà∑Êï∞ÊçÆ');
    this.refreshProfile();
    // ÈÄöÁü•ÂØºËà™Ê†èÊõ¥Êñ∞Áä∂ÊÄÅ
    tabBarManager.setCurrentIndexByPath('/pages/user/home');
  },
  
  onUnload() {
    // ÁßªÈô§‰∫ã‰ª∂ÁõëÂê¨
    uni.$off('AUTH_CHANGED', this.onAuthChanged);
  },
  
  methods: {
    // Áªü‰∏ÄÂà∑Êñ∞ÂáΩÊï∞ - Âü∫‰∫é isAuthed() Âíå getLoginData() Êõ¥Êñ∞ÊâÄÊúâÁä∂ÊÄÅ
    refreshProfile() {
      // ÂºÄÂßãÂä†ËΩΩ
      this.isLoading = true;
      
      // Ê®°ÊãüÂºÇÊ≠•Âä†ËΩΩÔºàÁ°Æ‰øùÈ™®Êû∂Â±èÂèØËßÅÔºâ
      setTimeout(() => {
        // Ëé∑ÂèñÊúÄÊñ∞ÁôªÂΩïÁä∂ÊÄÅ
        this.authed = isAuthed();
        const loginData = getLoginData();
      
      if (this.authed) {
        // Â∑≤ÁôªÂΩïÁä∂ÊÄÅ
        const uid = loginData.uid || '';
        const userInfo = loginData.userInfo || {};
        
        // ËÆ°ÁÆóÊòæÁ§∫ÂêçÁß∞Ôºönickname > username > uidÂêé6‰ΩçÔºà‰∏çÂä†ÂâçÁºÄÔºâ
        let displayName = '';
        if (userInfo.nickname) {
          displayName = userInfo.nickname;
        } else if (userInfo.username) {
          displayName = userInfo.username;
        } else if (uid) {
          // uidÂÖúÂ∫ïÔºöÂè™ÊòæÁ§∫Âêé6‰ΩçÔºå‰∏çÂä†ÂâçÁºÄ
          displayName = uid.slice(-6);
        } else {
          displayName = 'Áî®Êà∑';
        }
        
        // Êõ¥Êñ∞Áä∂ÊÄÅÊï∞ÊçÆ
        this.name = displayName;
        this.uid = uid;
        this.avatar = userInfo.avatar || '';
        
        // Âä†ËΩΩËÆ¢ÈòÖËÆæÁΩÆ
        this.loadSubscriptionSettings();
        
      } else {
        // Êú™ÁôªÂΩïÁä∂ÊÄÅ
        this.name = 'Êú™ÁôªÂΩï';
        this.uid = '';
        this.avatar = '';
      }
      
        // ËæìÂá∫Êó•Âøó
        console.log(`[PROFILE] refreshProfile authed=${this.authed} name=${this.name} uid=${this.uid}`);
        
        // Âº∫Âà∂Êõ¥Êñ∞ËßÜÂõæ
        this.$forceUpdate();
        
        // ÁªìÊùüÂä†ËΩΩ
        this.isLoading = false;
      }, 300); // 300msÂª∂ËøüÔºåÁ°Æ‰øùÈ™®Êû∂Â±èÂèØËßÅ
    },
    
    // ÁôªÂΩïÁä∂ÊÄÅÂèòÊõ¥‰∫ã‰ª∂Â§ÑÁêÜ
    onAuthChanged(data) {
      console.log(`[PROFILE] on AUTH_CHANGED authed=${data.authed} ‚Üí refresh`);
      // Á´ãÂç≥Âà∑Êñ∞Áî®Êà∑Êï∞ÊçÆ
      this.refreshProfile();
    },
    
    // Â§ÑÁêÜÁôªÂΩï/ÈÄÄÂá∫ÁôªÂΩï
    async handleAuthAction() {
      if (this.authed) {
        // ÈÄÄÂá∫ÁôªÂΩï
        await this.handleLogout();
      } else {
        // Ë∑≥ËΩ¨ÁôªÂΩï
        this.handleLogin();
      }
    },
    
    // Â§ÑÁêÜÁôªÂΩï
    handleLogin() {
      console.log('[PROFILE] Ë∑≥ËΩ¨ÁôªÂΩïÈ°µ');
      uni.navigateTo({
        url: '/pages/login/login?from=' + encodeURIComponent('/pages/user/home')
      });
    },
    
    // Â§ÑÁêÜÈÄÄÂá∫ÁôªÂΩï
    async handleLogout() {
      try {
        uni.showLoading({
          title: 'ÈÄÄÂá∫‰∏≠...'
        });
        
        // Ê∏ÖÈô§Êú¨Âú∞ÁôªÂΩïÊï∞ÊçÆ - Ëøô‰ºöËá™Âä®Ëß¶Âèë AUTH_CHANGED ‰∫ã‰ª∂
        clearLoginData();
        
        console.log('[PROFILE] logout done');
        
        // Á´ãÂç≥Âà∑Êñ∞Áî®Êà∑Êï∞ÊçÆ
        this.refreshProfile();
        
        uni.hideLoading();
        
        uni.showToast({
          title: 'Â∑≤ÈÄÄÂá∫ÁôªÂΩï',
          icon: 'success',
          duration: 2000
        });
        
      } catch (error) {
        console.error('[PROFILE] ÈÄÄÂá∫ÁôªÂΩïÂ§±Ë¥•:', error);
        uni.hideLoading();
        uni.showToast({
          title: 'ÈÄÄÂá∫Â§±Ë¥•',
          icon: 'none',
          duration: 2000
        });
      }
    },
    
    // È°µÈù¢ÂØºËà™
    navigateTo(url) {
      console.log('[PROFILE] ÂØºËà™Âà∞:', url);
      uni.navigateTo({ url });
    },
    
    // ÊòæÁ§∫ÈóÆÈ¢òÂèçÈ¶à - Ë∑≥ËΩ¨Âà∞Áã¨Á´ãÈ°µÈù¢
    showFeedback() {
      console.log('[PROFILE] click feedback ‚Üí /pages-sub/other/feedback');
      uni.navigateTo({
        url: '/pages-sub/other/feedback'
      });
    },
    
    // ÊòæÁ§∫ËÆ¢ÈòÖËÆæÁΩÆ
    showSubscriptionSettings() {
      if (!this.authed) {
        this.handleLogin();
        return;
      }
      this.showSubscriptionPopup = true;
    },
    
    // Âä†ËΩΩËÆ¢ÈòÖËÆæÁΩÆ
    async loadSubscriptionSettings() {
      try {
        if (typeof subscribeAPI !== 'undefined' && subscribeAPI.getSettings) {
          const settings = await subscribeAPI.getSettings();
          this.subscriptionSettings = { ...this.subscriptionSettings, ...settings };
        }
      } catch (error) {
        console.error('[PROFILE] Âä†ËΩΩËÆ¢ÈòÖËÆæÁΩÆÂ§±Ë¥•:', error);
      }
    },
    
    // Êõ¥Êñ∞ËÆ¢ÈòÖËÆæÁΩÆ
    async updateSubscription() {
      try {
        if (typeof subscribeAPI !== 'undefined' && subscribeAPI.updateSettings) {
          await subscribeAPI.updateSettings(this.subscriptionSettings);
          console.log('[PROFILE] ËÆ¢ÈòÖËÆæÁΩÆÂ∑≤Êõ¥Êñ∞');
        }
      } catch (error) {
        console.error('[PROFILE] Êõ¥Êñ∞ËÆ¢ÈòÖËÆæÁΩÆÂ§±Ë¥•:', error);
      }
    }
  }
};
</script>

